body {
  margin: 0;
  padding: 0;
  /* background: radial-gradient(rgb(84, 193, 243), rgb(2, 40, 121)); */
  background-color: rgb(10,10,43);
}
#root {
  background-image: url(data:image/png;base64,);
  background: radial-gradient(rgb(84, 193, 243), rgb(2, 40, 121));
  padding-bottom: 50px;
  position: relative;
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
  -webkit-overflow-y: scroll;
  -moz-overflow-y: scroll;
  -webkit-overflow-x: hidden;
  -moz-overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  /* font-family: 'Montserrat', sans-serif; */
  /* animation: changeColor 60s infinite; */
}

@keyframes changeColor {
  0% {
    background: radial-gradient(rgb(84, 193, 243), rgb(2, 40, 121)); 
  }
  25% {
    background: radial-gradient(rgb(243, 84, 145), rgb(121, 2, 32)); 
  }
  50% {
    background: radial-gradient(rgb(255, 243, 140), rgb(168, 157, 3)); 
  }
  75% {
    background: radial-gradient(rgb(95, 252, 81), rgb(43, 146, 2)); 
  }
  100% {
    background: radial-gradient(rgb(120, 98, 245), rgb(38, 1, 124)); 
  }
}